.reviews {
  position: relative;

  &.section-offset {
    @media (max-width:500px) {
      margin-bottom: 8px;
    }
    @media (max-width:320px) {
      margin-bottom: 8px;
    }
  }

  &__container {
    max-width: 1200px;
    margin: 0 auto;
    padding: 0 20px;
    @media (max-width:768px) {
      padding: 0 14px;
    }
    @media (max-width:320px) {
      padding: 0 10px;
    }
  }

  &__grid {
    display: grid;
    grid-template-columns: repeat(12, 8.3333333333%);

    @media (max-width:1200px) {
      // grid-template-columns: repeat(8, 12.5%);
    }
    @media (max-width:768px) {
      grid-template-columns: repeat(8, 12.5%);
    }
  }

  &__title {
    display: inline-block;
    grid-column: span 3;
    @media (max-width:1200px) {
      grid-column: span 12;
    }
  }

  &__swiper {
    grid-column: span 9;
    width: 100%;
    height: auto;
    padding-top: 14px;
    margin-left: 10px;
    padding-bottom: 62px;
    overflow-y: visible;
    // overflow-y: initial;

    @media (max-width:1200px) {
      grid-column: 3/-1;
      // min-width: 870px;
      // display: flex;
      // margin-left: 0;
      // padding-bottom: 70px;
    }
    @media (max-width:992px) {
      grid-column: 2/-1;
    }
    @media (max-width:768px) {
      grid-column: span 8;
      margin-left: 0;
      padding-bottom: 70px;
    }

    & .swiper-slide {
      // display: grid;
      display: flex;
      // grid-template-columns: repeat(9, 11.11111111111111%);
      @media (max-width:768px) {
        // grid-template-columns: repeat(8, 12.5%);
        // grid-template-columns: minmax(140px, auto) 1fr;
        display: flex;
      }
      @media (max-width:500px) {
        flex-direction: column;
      }

      & img {
        display: block;
        // min-width: 140px;
      }
    }
  }

  &__img {
    // grid-column: span 3;
    margin-top: 6px;
    // width: 260px;
    // flex-basis: 369%;
    flex-shrink: 0;
    min-width: 140px;
    @media (max-width:992px) {
      flex-shrink: 1;
      min-width: 170px;
    }
    @media (max-width:768px) {
      grid-column: span 2;
      // grid-column: span 1;
      // margin-right: 14px;
    }
    @media (max-width:500px) {
      width: 140px;
      min-width: auto;
    }

    &-author {
      position: relative;
      max-width: 159px;
      margin: 0;
      font-weight: 600;
      font-size: 18px;
      line-height: 22px;
      color: var(--black-color);

      &::after {
        content: "";
        position: absolute;
        // left: 144px;
        left: auto;
        right: 0;
        bottom: 3px;
        width: 14px;
        height: 14px;
        background-image: url(../img/air-black.svg);
        @media (max-width:768px) {
          left: auto;
          right: 0;
        }
      }

      &.-age {
        margin: 0;
        font-size: 14px;
        line-height: 17px;
        &::after {
          display: none;
        }
      }
    }
  }

  &__foto {
    margin-bottom: max(6%, 13.6px);
  }

  &-text {
    margin: 0;
    grid-column: span 6;
    margin-left: 41px;
    @media (max-width:992px) and (min-width:769px) {
      margin-left: 25px;
      font-size: 17px;
    }
    @media (max-width:768px) {
      margin-top: 4px;
      margin-left: 25px;
      line-height: 150%;
    }
    @media (max-width:500px) {
      grid-column: span 8;
      margin-left: 0;
      margin-top: 16px;
      line-height: 143%;
    }
    @media (max-width:320px) {
      grid-column: span 8;
      margin-left: 0;
      margin-top: 16px;
      line-height: 143%;
    }
  }

  &__buttons {
    position: absolute;
    bottom: 0;
    right: 25%;
    width: 222px;
    height: 35px;
    @media (max-width:768px) {
      right: 22%;
    }
    @media (max-width:500px) {
      // display: flex;
      // position: static;
      // grid-column: 5/9;
      // grid-row: 2/4;

      top: 157px;
      right: calc((100% - 165px) / 2);
      width: 141px;
      height: 31px;
      transform: translateX(50%);
      z-index: 10;
    }
    @media (max-width:320px) {
      // top: 123px;
      // right: -1%;
      // width: 141px;
      // height: 31px;
    }






    & svg {
      height: 100%;
    }
  }

  & .swiper-button-prev,.swiper-button-next {
    position: absolute;
    height: 100%;
    &::after {
     display: none;
    }
  }
  & .swiper-button-prev {



  }
  & .swiper-button-next {

  }

}
