.hero {
  --padding-left: 2%;
  position: relative;
  margin-bottom: 119px;
  color: var(--light-color);
  @media (max-width: 1400px) {
    // --padding-left: 15.6px 2 39.3px 8
    --padding-left: clamp(15.6px, calc(68.8px - 3.8vw), 39.3px);
  }
  @media (max-width: 768px) {
    // --padding-left: 39.3px
    --padding-left: clamp(24px, 5.1171875vw, 39.3px);
    margin-bottom: clamp(45px, 15.5vw, 119px);
  }
  @media (max-width: 320px) {
    margin-bottom: 45px;
    // margin-bottom: clamp(45px, 15.5vw, 119px);
  }


  &__container {
    position: relative;
    padding-top: 210px;
    padding-bottom: 84.5px;
    border-radius: 20px;
    background: no-repeat url("../img/ellipse.png") 28.9% 0%,
                no-repeat url("../img/ellipse-bottom.png") right 24.8% bottom 0%;
    background-color: var(--charade-color);

    @media (min-width: 1400px) {
      padding-bottom: 80px;
    }
    @media (max-width: 1400px) {
      padding-bottom: clamp(80px, calc(164px - 6vw), 117px);
    }
    @media (max-width: 768px) {
      padding-top: clamp(96px, 27.99479166666667vw, 215px);
    }
    @media (max-width: 760px) {
      padding-bottom: 8%;
    }
    @media (max-width: 576px) {
      padding-top: clamp(96px, 20.83333333333333vw, 120px);
    }
    @media (max-width: 576px) {
      padding-bottom: 6%;
    }
    @media (max-width: 320px) {
      padding-bottom: 15px;
    }
  }


  &__grid {
    position: relative;
    display: grid;
    grid-template-columns: repeat(12, 8.333333333333333%);
    max-width: 1171px;
    margin: 0 auto;
    padding-bottom: min(2.5%, 33.5px);
    @media (max-width: 1242px) {
      overflow: hidden;
    }
    &::before {
      position: absolute;
      top: -12px;
      right: 0.6%;
