        overflow: hidden;
        content: "{ } { } { } { } { } { } { } { } { } { }";
        width: 409px;
        // right: 6px;
      }
      @media (max-width: 824px) {
        right: -2.4%;
      }
      @media (max-width: 768px) {
        right: -3.4%;
        font-size: max(2.604166666666667vw, 10.2185px);
      }
      @media (max-width: 767px) {
        content: "{ } { } { } { } { } { } { } { }";
        width: max(41%, 153px);
        right: max(0px, calc(36.8px - (76px - 10vw)));
        top: -7px;
      }
      @media (max-width: 320px) {
        top: -7px;
        right: 0%;
        // width: 153px;
      }
    }
