  &__title {
    grid-column: span 12;
    display: grid;
    grid-template-columns: repeat(12, 8.333333333333333%);
    margin: 0;
  }

  &__text {
    color: var(--light-color);
    font-size: 110px;
    font-weight: 500;
    letter-spacing: -0.06em;

    @media (max-width: 1400px) {
      font-size: 7.857142857142857vw;
    }

    &-brace {
      font-weight: 400;
    }

    &--one {
      grid-column: span 6;
      padding-left: 5px;
      padding-top: 37px;

      @media (max-width: 1302px) {
        padding-left: 29px;
      }

      @media (max-width: 768px) {
        // padding-left: 15px, 3.776041666666667vw, 29px;
        // padding-left: max(15px, 3.776041666666667vw);
        padding-left: clamp(15px, 3.776041666666667vw, 29px);
        // padding-top: 42px;
        padding-top: clamp(32px, 5.46875vw, 42px);
      }
      // @media (max-width: 320px) {
      //   padding-top: 32px;
      // }
    }

    &--two {
      // grid-column: span 12;
      grid-column-start: 5;
      grid-column-end: 13;
      justify-self: start;
      // margin-top: -8.75px;
      margin-top: clamp(-20.2188px, calc(24.7812px - 4vw), -8.75px);
      padding-left: var(--padding-left);
      text-indent: -1%;

      @media (max-width: 768px) {
        margin-top: clamp(-8.75px, -1.7%, -2px);
      }

      // padding-right: 67px;
      // transform: translateY(-26.08%);

      @media (max-width: 320px) {
        // margin-top: -2px;
        // padding-left: 8%;
        // transform: translateY(-18.08%);
      }
    }

    &-transform {
      grid-column: span 12;
      padding-left: 2%;
      margin-top: -1%;
      // padding-left: 20px;
      font-weight: 700;
      // background: url(../img/hero-rectangle.jpg) no-repeat;
      background-color: var(--charade-color);
      // transform: translateY(-12px) translateX(6px) rotate(-2.19deg);
      // transform: translate(0.5119999997999999%, -9.486166007905138%) rotate(-2.19deg);
      transform: rotate(-2.19deg);

      @media (max-width: 1400px) {
        padding-left: clamp(23.4px, calc(58.4px - 2.5vw), 37px);
      }
      @media (max-width: 1302px) {
        // padding-left: 34px;
      }

      @media (max-width: 768px) {
        margin-top: -0.5%;
        // padding-left: 37px;
        padding-left: clamp(16px, 4.817708333333333vw, 37px);
        transform: rotate(-2.19deg);
      }
    }

    &-java {
      position: relative;

      &::after {
        content: "";
        position: absolute;
        z-index: -1;
        top: 4%;
        left: -3%;
        width: 106%;
        height: 97%;
        background-color: var(--monza-color);
        transform: rotate(-2.19deg);
      }
    }
  }

  &__descr {
    position: relative;
    grid-column-start: 5;
    grid-column-end: 13;
    max-width: 371px;
    margin-top: 1.5%;
    padding-left: var(--padding-left);

    @media (max-width: 1400px) {
      margin-top: clamp(11.7031px, calc(151.7031px - 10vw), 26.75px);
    }

    @media (max-width: 768px) {
      // max-width: 399px;
      max-width: 52vw;
      margin-top: clamp(10px, 3.48vw, 26.75px);
      padding-bottom: 1%;
      font-size: clamp(13px, 2.4vw, 20px);
    }
    @media (max-width: 320px) {
      margin-top: 10px;
    }

    &::after {
      position: absolute;
      top: 17%;
      right: calc(100% + 46px);
      width: 390px;
      height: 142px;
      overflow: hidden;
      direction: rtl;
      content: "+ + + + + + + + + + + + + + + + + + + + + + + + + + + + + + + + + + + + + + + +";
      font-size: 20px;
      letter-spacing: 0.5em;
      line-height: clamp(20px, 190%, 38px);
      @media (max-width: 1324px) {
        width: 362px;
      }
      @media (max-width: 1232px) {
        width: 318px;
      }
      @media (max-width: 1200px) {
        right: calc(100% + 15px);
      }
      @media (max-width: 1120px) {
        width: 294px;
      }
      @media (max-width: 992px) {
        right: calc(100% - 10px);
      }
      @media (max-width: 858px) {
        width: 294px;
        right: calc(100% - 20px);
      }
      @media (max-width: 768px) {
        top: -8%;
        right: calc(100% - 20px);
        width: 294px;
      }
      @media (max-width: 767px) {
        width: 254px;
      }
      @media (max-width: 662px) {
        // font-size: 15px;
        font-size: clamp(10px, 2.99vw, 20px);;
        width: 37vw;
        // height: 21.45vw;
        height: 24vw;
      }
      @media (max-width: 320px) {
        font-size: 10px;
        line-height: 20px;
        letter-spacing: 5px;
      }
    }
  }

  &__button-grid {
    grid-column-start: 5;
    grid-column-end: 13;
    justify-self: start;
    margin-left: var(--padding-left);
    margin-top: 1%;

    @media (max-width: 768px) {
      margin-top: 1.8%;
    }
    @media (max-width: 500px) {
      grid-column: span 12;
      justify-self: center;
      width: 84%;
      margin: 0;
      margin-top: 11%;
      font-size: clamp(14px, 4vw, 20px);
    }
    @media (max-width: 370px) {
      margin-top: 4%;
    }
    @media (max-width: 320px) {
      margin-top: 3%;
    }
  }

  &__btns {
    position: absolute;
    z-index: 1;
    left: 50%;
    transform: translateX(-50%);
    bottom: 0;
    display: flex;
    align-items: center;
    gap: 25px;
    height: 0;

    @media (max-width:924px) {
      gap: 10px;
    }

    @media (max-width:760px) {
      display: none;
    }
  }

  &__ellipse {
    position: absolute;
    top: -95px;
    left: 28.9%;
    width: 339px;
    height: 190px;
    background: rgba(255, 255, 255, 0.2);
    filter: blur(50px);

    &--bottom {
    top: auto;
    left: auto;
    bottom: -95px;
    right: 24.8%;
    }
  }

}
