.about {
  --margin-left-768: 25%;
  margin-bottom: clamp(71px, 7.285714285714286vw, 102px);
  @media (max-width: 768px) {
    --margin-left-768: max(0px, 183.6px - (384px - 50vw));
    margin-bottom: 71px;
    margin-bottom: clamp(54px, 9.244791666666667vw, 71px);
  }
  @media (max-width: 320px) {
    margin-bottom: 54px;
  }

  &__container {
    max-width: 1200px;
    margin: 0 auto;
    padding: 0 20px;
    @media (max-width: 320px) {
      padding: 0 10px;
    }
  }

  &__grid {
    position: relative;
    display: grid;
    grid-template-columns: repeat(12, 8.333333333333333%);
    // gap: 60px;
    &::before {
      position: absolute;
      top: -8px;
      right: -4.4%;
      width: 616px;
      height: 94px;
      overflow: hidden;
      content: "{ } { } { } { } { } { } { } { } { } { } { } { } { } { } { } { }";
      font-size: 20px;
      letter-spacing: 0.67em;
      line-height: 2.35;
      // word-spacing: -0.099px;
      @media (max-width: 1242px) {
        overflow: hidden;
        content: "{ } { } { } { } { } { } { } { } { } { }";
        width: 409px;
        // right: 6px;
      }
      @media (max-width: 824px) {
        right: -2.4%;
      }
      @media (max-width: 768px) {
        top: 2px;
        right: -6.4%;
        // font-size: max(2.604166666666667vw, 10.2185px);
      }
      @media (max-width: 767px) {
        content: "{ } { } { } { } { } { } { } { }";
        width: 299px;
        right: 3.4%;
      }
      @media (max-width: 592px) {
        content: "{ } { } { } { } { } { } { } { }";
        width: 299px;
        height: 47px;
        right: 0;
        // top: 10vw;
        top: 59px;
      }
      @media (max-width: 592px) {
        content: "{ } { } { } { } { } { } { } { }";
        // top: 6%;
      }
      @media (max-width: 425px) {
        content: "{ } { } { } { } { } { } { } { }";
        // top: 5%;
      }
      @media (max-width: 398px) {
        left: 50%;
        transform: translateX(-50%);
      }
      @media (max-width: 320px) {
        // top: 8%;
        // right: -5%;
        // width: 153px;
      }
    }
  }

  &__title {
    grid-column: span 12;
    margin: 0;
    margin-left: -6px;
    margin-bottom: 39px;
    padding-top: 16px;
    // font-size: 60px;
    font-size: clamp(50px, 4.285714285714286vw, 60px);
    font-weight: 500;
    letter-spacing: -0.06em;
    color: var(--charade-color);

    @media (max-width:1400px) {
      // margin-left: clamp(-6px, calc(50px - 4vw), 14px);
    }
    @media (max-width:768px) {
      font-size: clamp(30px, 6.510416666666667vw, 50px);
      padding-top: 20px;
      margin-bottom: 50px;
    }
    @media (max-width:320px) {
      margin-left: 0;
      margin-bottom: 71px;
    }
  }

  &__subtitle {
    grid-column: span 12;
    margin: 0;
    // padding-top: 39px;
    font-weight: 600;
    font-size: 20px;

    @media (max-width: 768px) {
      font-size: 18px;
    }
    @media (max-width: 767px) {
      line-height: 150%;
    }
    @media (max-width: 767px) {
      // margin-left: -10px;
      margin-bottom: -4px;
    }
    @media (max-width: 320px) {

    }
  }

  &__text {
    grid-column: span 6;
    margin: 0;
    font-size: 18px;
    line-height: 150%;
    color: var(--black-color);

    @media (max-width: 768px) {
      font-size: 16px;
    }
    @media (max-width: 500px) {
      font-size: 14px;
    }

    &-long {
      grid-column: span 12;
      padding-top: 9px;
      @media (min-width: 321px) {
        margin-bottom: min(65px, 4.642857142857143vw);
      }
      @media (max-width: 768px) {
        grid-column: span 12;
      }
      @media (max-width: 320px) {
        // margin-left: -10px;
        margin-bottom: 6.642857vw;
      }
      &span {
        @media (max-width: 320px) {
          margin-right: -4px;
          margin-bottom: 6vw;
        }
      }
      &-br {
        display: none;
        @media (max-width: 405px) {
          display: block;
        }
      }
    }

    &-one {
      @media (min-width: 769px) {
        padding-right: 20px;
      }

      @media (max-width: 768px) {
        // grid-column-start: 4;
        // grid-column-end: 13;
        grid-column: span 12;
        margin-left: var(--margin-left-768);
        margin-bottom: 3%;
      }
      @media (max-width: 320px) {
        margin-bottom: 4%;
      }
    }

    &--bold {
      font-weight: 600;

      &--const {
        font-size: 18px;
        @media (max-width: 450px) {
          font-size: 16px;
        }
      }
    }

    &-pl {
      margin-left: 0;

      @media (max-width: 768px) {
        // grid-column-start: 4;
        // grid-column-end: 13;
        grid-column: span 12;
        margin-left: var(--margin-left-768);
      }
    }

    &-task {
      margin-bottom: 54px;
      font-size: 18px;
      @media (max-width: 768px) {
        margin-bottom: max(39px, 7.16vw);
      }
    }
  }

  &__text-9 {
    position: relative;
    grid-column-start: 4;
    grid-column-end: 13;
    // padding-top: 12%;
    padding-top: clamp(43.7px, 10%, 104.375px);
    // 104.375
    margin-left: 1%;
    margin-bottom: max(120px, 16%);
    max-width: 430px;
    // padding-right: 49%;
    @media (max-width: 768px) {
      padding-top: 8%;
    }
    @media (max-width: 320px) {
      margin-bottom: 36%;
      padding-top: 5%;
    }

    &::after {
      position: absolute;
      right: calc(97% + 4.64vw);
      bottom: -37px;
      width: 358px;
      height: 142px;
      overflow: hidden;
      content: "+ + + + + + + + + + + + + + + + + + + + + + + + + + + + + + + + + + + +";
      font-size: 20px;
      letter-spacing: 0.5em;
      line-height: clamp(20px, 200%, 38px);
      direction: rtl;

      @media (max-width: 1400px) {

      }
      @media (max-width: 1376px) {
        content: "+ + + + + + + + + + + + + + + + + + + + + + + + + + + + + + + +";
        width: 332px;
      }
      @media (max-width: 1298px) {
        width: 293px;
      }
      @media (max-width: 1222px) {
        width: 256px;
      }
      @media (max-width: 1086px) {
        width: 217px;
      }
      @media (max-width: 767px) {
        display: none;
      }
  }

    & span {
      display: block;
    }
  }

  &__text-span {
    position: relative;

    &::before {
      position: absolute;
      top: -37px;
      content: "{";
      font-weight: 700;
      font-size: 20px;
      color: var(--monza-color);
      @media (max-width: 320px) {
        top: -27px;
      }
    }

    &::after {
      position: absolute;
      left: 0;
      bottom: -33px;
      content: "}";
      font-weight: 700;
      font-size: 20px;
      color: var(--monza-color);
    }
  }

  &__text-result {
    margin: 0;
    grid-column-start: 4;
    grid-column-end: 13;
    max-width: 434px;
    margin-left: 1%;
    margin-bottom: 15px;
    // padding-right: 372px;
    line-height: 22px;
    @media (max-width: 450px) {
      line-height: 19px;
      margin-left: 0;
      margin-right: -11px;
    }
  }

  &__result-big {
    margin: 0;
    text-transform: uppercase;
    font-weight: 600;
    line-height: 102%;
    letter-spacing: -0.02em;
    color: var(--charade-color);
    @media (min-width: 768px) {
      font-size: clamp(32px, 3.571428571428571vw, 50px);
    }
    @media (max-width: 767px) {
      font-size: clamp(20px, 5vw, 32px);
    }
    &.one {
      @media (max-width: 320px) {
        margin-bottom: 5px;
      }
    }
    &.long {
      grid-column: span 12;
      @media (max-width: 768px) {
        // margin-left: -6px;
        margin-left: clamp(0px, 5px - (8.448px - 1.1vw), 5px);
      }
      @media (max-width: 320px) {
        // margin-left: -11px;
      }
    }
    &.short {
      grid-column-start: 4;
      grid-column-end: 13;
      margin-left: 1%;
    }
    &.red {
      color: var(--monza-color);
    }
    &-br {
      display: none;
    }
  }

  &__text-bold-lg {
    margin: 0;
    font-weight: 600;
    font-size: 24px;
    letter-spacing: -0.02em;
    color:  var(--black-color);
    @media (max-width: 768px) {
      font-size: clamp(18px, 24px - (10.752px - 1.4vw), 24px);
    }
  }

  &__text--action {
  grid-column: span 2;
  margin-bottom: 21px;
  margin-top: 112px;

  @media (max-width: 1058px) {
    grid-column: span 12;
    text-align: center;
    margin-top: max(61px, 14%);
    margin-bottom: 3.6%;
  }
  @media (max-width: 768px) {
    // margin-bottom: 26px;
    margin-bottom: clamp(23px, 26px - (8.448px - 1.1vw), 26px);
  }
  @media (max-width: 404px) {
    text-align: start;
    margin-left: 0;
  }
  @media (max-width: 320px) {
    // margin-bottom: 23px;
  }
  }

  &__action {
    grid-column: span 12;
    display: flex;
    justify-content: center;
    align-items: stretch;
    max-height: 100px;

    @media (max-width: 1058px) {
      flex-direction: column;
      align-items: center;
      max-height: none;
      // max-width: 360px;
      width: 50%;
      margin: 0 auto;
    }
    @media (max-width: 768px) {
      width: 360px;
    }
    @media (max-width: 404px) {
      width: 100%;
      // width: auto;
    }

    &-item {
      display: flex;
      justify-content: center;
      align-items: center;
      width: 20%;
      flex-grow: 1;
      background-color: var(--Indigo-color);
      text-align: center;
      font-weight: 500;
      font-size: 20px;
      border-radius: 1em;
      padding: 2.3% 0;
      color: var(--light-color);

      @media (max-width: 1058px) {
        width: 100%;
        padding: 17px 0;
      }

      &.fat {
        @media (max-width: 404px) {
          padding: 17px 41px;
        }
      }
      & span {
        width: min-content;
        @media (max-width: 1058px) {
          width: max-content;
        }
      }
    }

    &-rect {
      position: relative;
      background-color: var(--Indigo-color);
      width: 28px;

      &::after {
        position: absolute;
        content: "";
        top: 0;
        left: 0;
        width: 100%;
        height: 35%;
        background-color: var(--light-color);
        border-bottom-left-radius: 100%;
        border-bottom-right-radius: 100%;
      }
      &::before {
        position: absolute;
        content: "";
        left: 0;
        bottom: 0;
        width: 100%;
        height: 35%;
        background-color: var(--light-color);
        border-top-left-radius: 100%;
        border-top-right-radius: 100%;
      }

      @media (max-width: 1058px) {
        width: 100%;
        height: 19px;

        &.one {
          &::after {
            width: 44%;
            height: 100%;
            border-bottom-left-radius: 0;
            border-bottom-right-radius: 1em;
            border-top-right-radius: 1em;
          }
          &::before {
            left: auto;
            right: 0;
            top: 0;
            width: 50%;
            height: 100%;
            border-top-left-radius: 1em;
            border-top-right-radius: 0;
            border-bottom-left-radius: 1em;
          }
        }
        &.two {
          &::after {
            width: 53%;
            height: 100%;
            border-bottom-left-radius: 0;
            border-bottom-right-radius: 1em;
            border-top-right-radius: 1em;
          }
          &::before {
            left: auto;
            right: 0;
            top: 0;
            width: 38%;
            height: 100%;
            border-top-left-radius: 1em;
            border-top-right-radius: 0;
            border-bottom-left-radius: 1em;
          }
        }
        &.three {
          &::after {
            width: 29%;
            height: 100%;
            border-bottom-left-radius: 0;
            border-bottom-right-radius: 1em;
            border-top-right-radius: 1em;
          }
          &::before {
            left: auto;
            right: 0;
            top: 0;
            width: 61%;
            height: 100%;
            border-top-left-radius: 1em;
            border-top-right-radius: 0;
            border-bottom-left-radius: 1em;
          }
        }
        &.four {
          &::after {
            width: 51%;
            height: 100%;
            border-bottom-left-radius: 0;
            border-bottom-right-radius: 1em;
            border-top-right-radius: 1em;
          }
          &::before {
            left: auto;
            right: 0;
            top: 0;
            width: 42%;
            height: 100%;
            border-top-left-radius: 1em;
            border-top-right-radius: 0;
            border-bottom-left-radius: 1em;
          }
        }
      }
    }
  }
  &__text-bold-lg.got {
    grid-column: span 12;
    margin-bottom: 26px;

    @media (min-width: 769px) {
      margin-top: 96px;
    }

    @media (max-width: 768px) {
      margin-left: clamp(-10px, calc(13.4px - 3vw), 0px);
      margin-top: clamp(109px, calc(285.64px - 23vw), 202px);
      margin-bottom: max(3.385416666666667vw, 15px);
    }
  }

  &__got {
    &-descr {
      position: relative;
      grid-column: span 12;
      margin-left: 43px;
      margin-bottom: 37px;
      @media (max-width: 768px) {
        // margin-left: 33px;
        margin-left: clamp(33px, calc(148.2px - 15vw), 84px);
      }
      @media (max-width: 320px) {
        margin-bottom: 17px;
      }
      &::after {
        position: absolute;
        top: -20px;
        left: max(535px, 47vw);
        width: 390px;
        height: 142px;
        overflow: hidden;
        content: "+ + + + + + + + + + + + + + + + + + + + + + + + + + + + + + + + + + + + + + + +";
        font-size: 20px;
        letter-spacing: 0.5em;
        line-height: clamp(20px, 200%, 38px);

        @media (max-width: 1324px) {
          content: "+ + + + + + + + + + + + + + + + + + + + + + + + + + + + + + + + + + + +";
          width: 362px;
        }
        @media (max-width: 1136px) {
          width: 347px;
        }
        @media (max-width: 1016px) {
          width: 300px;
        }
        @media (max-width: 902px) {
          // content: "+ + + + + + + + + + + + + + + + + + + + + + + + + + + +";
          width: 294px;
        }
        @media (max-width: 862px) {
          width: 243px;
        }
        @media (max-width: 816px) {
          width: 205px;
        }
        @media (max-width: 768px) {

          top: -16px;
          width: 294px;
        }
        @media (max-width: 767px) {
          display: none;
        }
        @media (max-width: 590px) {
          display: block;
          left: 0px;
          top: -198px;
        }
        @media (max-width: 360px) {
          width: 245px;
        }
        @media (max-width: 320px) {
          left: 0px;
          top: -198px;
        }
      }
      &::before {
        position: absolute;
        content: "";
        left: -40px;
        bottom: -21px;
        display: block;
        width: 512px;
        height: 11px;
        background-image: url(../img/line.svg);
        background-repeat: no-repeat;
        background-size: contain;
        @media (max-width: 568px) {
          width: 95vw;
          left: auto;
          right: -5px;
          background-image: url(../img/line-small.svg);
        }
        @media (max-width: 320px) {
          left: -85px;
          bottom: -10px;
          width: 96vw;
          height: 11px;
          background-image: url(../img/line-small.svg);
        }
      }
      & p {
        margin: 0;
        font-weight: 600;
        line-height: 168.02%;
        @media (max-width: 768px) {
          font-size: clamp(16px, 20px - (10.752px - 1.4vw), 20px);
          line-height: clamp(19px, 168.02% - (15.36px - 2vw), 168.02%);
          // margin-bottom: clamp(0px, calc(30.72px - 4vw), 15px);
          margin-bottom: clamp(0px, 30.72px - 4vw, 15px);
        }

        &.second {
          position: relative;
          &::before {
            position: absolute;
            content: "";
            top: -7px;
            left: -35px;
            display: block;
            width: 20px;
            height: 20px;
            background-image: url("../img/plus.svg");
            background-repeat: no-repeat;
            background-size: cover;
            @media (max-width: 320px) {
              top: -19px;
              left: -30px;
            }
          }
          &::after {
            position: absolute;
            content: "";
            top: 35px;
            left: -34px;
            display: block;
            width: 22px;
            height: 22px;
            background-image: url("../img/plus2.svg");
            background-repeat: no-repeat;
            background-size: cover;
            @media (max-width: 320px) {
              top: 18px;
              left: -30px;
            }
          }
        }
      }
    }
    &-result {
      position: relative;
      grid-column: span 12;
      margin-left: 43px;
      margin-bottom: max(53px, 9.75vw);
      @media (max-width: 768px) {
        margin-left: 33px;
      }
      @media (max-width: 320px) {
        margin-left: 43px;
      }
      &::before {
        position: absolute;
        content: "";
        top: 9px;
        left: -35px;
        display: block;
        width: 27px;
        height: 20px;
        background-image: url(../img/equally.svg);
        background-repeat: no-repeat;
        background-size: contain;
      }
    }
  }

  &__creator-text {
    grid-column: span 7;

    @media (max-width: 980px) {
      grid-column: span 12;
      margin-bottom: 6vw;
    }

    & p {
      // padding-right: 109px;
      padding-right: 7.8vw;
      @include mb(27px);

      @media (max-width: 980px) {
        padding-right: 0;
      }
      @media (max-width: 320px) {
        @include mb(22px);
      }
    }
  }

  &__creator-title {
    padding-top: 8.5vw;
    margin-bottom: 26px;
    @media (max-width: 980px) {
      padding-top: 2vw;
      margin-bottom: 31px;
    }
    @media (max-width: 350px) {
      margin-bottom: 39px;
      line-height: 85.52%;
    }
  }

  &__img {
    position: relative;
    grid-column: span 5;
    margin-left: 26px;
    margin-right: auto;
    margin-bottom: 113px;
    &::before {
      position: absolute;
      top: 69%;
      right: 119.6%;
      width: 616px;
      height: 94px;
      overflow: hidden;
      content: "{ } { } { } { } { } { } { } { } { } { } { } { } { } { } { } { }";
      font-size: 20px;
      letter-spacing: 0.67em;
      line-height: 2.35;
      direction: rtl;
      // word-spacing: -0.099px;
      @media (max-width: 1242px) {
        width: 575px;
      }
      @media (max-width: 1110px) {
        width: 495px;
        // right: 6px;
      }
      @media (max-width: 980px) {
        top: auto;
        // bottom: min(-30px, -19%);
        bottom: min(-93px, -17vw);
        right: 0;
        width: 616px;
      }
      @media (max-width: 716px) {
        width: 574px;
      }
      @media (max-width: 590px) {
        width: 486px;
      }
      @media (max-width: 486px) {
        right: 50%;
        transform: translateX(50%);
        width: 391px;
      }
      @media (max-width: 390px) {
        width: 315px;
        height: 50px;
        bottom: -64px;
      }
    }

    &-inner {
      position: relative;
      display: inline-block;

    }

    @media (max-width: 1200px) {
      margin-left: 0;
    }
    @media (max-width: 980px) {
      grid-column: span 12;
      margin-left: var(--margin-left-768);
      margin-bottom: 196px;
    }
    @media (max-width: 980px) {
      grid-column: span 12;
      margin-left: var(--margin-left-768);
      margin-bottom: clamp(102px, 26vw, 196px);
    }
    @media (max-width: 590px) {
      margin-left: calc(var(--margin-left-768) - 40px);
    }
    @media (max-width: 530px) {
      grid-column: span 12;
      width: 100%;
      margin-left: 0;
    }
    @media (max-width: 320px) {
      margin-bottom: 102px;
    }
  }

  &__result-big {
    &.req {
      line-height: 113%;
      @media (max-width: 980px) {
        line-height: 125%;
      }
      @media (max-width: 320px) {
        line-height: 108%;
      }
    }
    &.long.req {
      @media (max-width: 980px) {
        margin-left: -6px;
      }
      @media (max-width: 320px) {
        margin-left: 0;
        padding-right: 29px;
      }
    }
  }

}

.minus {
  margin: 0 -2px;
}
