.fade, .fade.animate-open {
  transition: none;
}

.graph-modal {

  color: var(--charade-color);
  background-color: rgba(0, 0, 0, 0.7);

  &__container {
    max-width: 440px;
    // width: auto;
    padding: 0;
    border-radius: 0;
  }
}

.modal {


  &__container {
    // max-width: 440px;
    min-height: 667px;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 0 22%;
    padding-top: 34px;
    @media (max-width:480px) {
      padding: 0 14%;
      padding-top: 34px;
    }
  }

  &__logo-link {
    margin-bottom: 57px;
  }

  &__title {
    margin-bottom: 28px;
    font-weight: 600;
    font-size: 24px;
    line-height: 29px;
    letter-spacing: -0.02em;
    &.forgot-password{
      margin-bottom: 46px;
    }
  }

  &__text {
    margin: 0;
    margin-bottom: 35px;
    text-align: center;
    font-size: 12px;
    line-height: 15px;
  }

  &__input {
    display: block;
    width: 100%;
    padding: 9px 5px;
    color: var(--jumbo-color);
    border: 1px solid var(--gallery-color);
    border-radius: 5px;
    font-size: 13px;
    line-height: 16px;
    &::placeholder {
      color: var(--jumbo-color);
      font-size: 13px;
      line-height: 16px;
    }
    &.just-validate-error-field {
      border: 1px solid var(--monza-color);
      color: var(--monza-color);
    }

    &-wrap {
      position: relative;
      width: 100%;
      @include mb (20px);
      &.forgot-password{
        margin-bottom: 27px;
      }
    }
  }

  &__links-wrap {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    margin-top: 139px;
    margin-bottom: 46px;

    .form-2 & {
      margin-top: 29px;
    }

    &.forgot-password{
      justify-content: center;
      margin-top: 130px;
    }
  }

  &__link {
    @include mr (5px);
    color: inherit;
    font-size: 12px;
    line-height: 15px;
    text-decoration: underline;
    cursor: pointer;
    @include hover {
      font-weight: 600;
    }
    // @media (max-width:) {

    // }
  }

  &__button {
    width: 100%;
    padding: 16px 10px;
    border-radius: 5px;
    color: var(--light-color);
    font-weight: 600;
    font-size: 14px;
    line-height: 17px;
    background-color: var(--monza-color);
  }
}

.just-validate-error-label {
  position: absolute;
  right: 0;
  bottom: -15px;
  font-size: 11px;
  line-height: 13px;
}
