.button {

  &-radius {
    border-radius: 1em;
    padding: 10px 30px;
    color: var(--charade-color);
    background-color: var(--light-color);

    &:hover {
      color: var(--light-color);
      background-color: var(--Indigo-color);
    }
  }

  &-rect {
    position: relative;
    padding: 16px 47px;
    border-radius: 10px;
    font-weight: 600;
    color: var(--light-color);
    background-color: var(--monza-color);

    // &:hover {
    //   color: var(--monza-color);
    //   background-color: var(--light-color);
    // }

    @include hover {
      color: var(--monza-color);
      background-color: var(--light-color);
    };

    @include hover {
      &.border {
        &::after {
        content: "";
        position: absolute;
        // z-index: -1;
        width: calc(100% + 6px);
        height: calc(100% + 6px);
        border-radius: 10px;
        border: 3px solid var(--monza-color);
        background-color: transparent;
        left: -3px;
        top: -3px;
        };

        & span::before {
          background-image: url("data:image/svg+xml,%3Csvg width='26' height='18' viewBox='0 0 26 18' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M5.04488 18C4.0268 17.9917 3.0681 17.7708 2.16877 17.3373C1.26952 16.8954 0.5466 16.2665 0 15.4506L1.13728 14.1758C1.64861 14.8897 2.23929 15.4336 2.90932 15.8076C3.57935 16.1815 4.3199 16.3685 5.13098 16.3685C5.38335 16.3685 5.62183 16.3526 5.84641 16.3209L5.04488 18ZM8.56927 10.6168V1.63175H1.79849V0H10.5V6.57219L8.56927 10.6168Z' fill='%23D0082C'/%3E%3Cpath d='M6.75 18L15.1651 0H17.0849L25.5 18H23.4488L15.7174 1.10572H16.5063L8.77489 18H6.75ZM10.0635 13.1914L10.642 11.6486H21.345L21.9236 13.1914H10.0635Z' fill='%23D0082C'/%3E%3C/svg%3E%0A");
        }
      }
    }
  }

  &-border {
    display: block;
    padding: 18px 60px;
    border: 2px solid currentColor;
    border-radius: 30px;
    text-align: center;
    cursor: pointer;
    white-space: nowrap;
    font-weight: 700;
    font-size: clamp(16px, 1.428571428571429vw, 20px);
    color: var(--charade-color);
    background-color: var(--light-color);

    // &:hover {
    //   color: var(--monza-color);
    // }

    @include hover {color: var(--monza-color)};

    @media (max-width:1224px) {
      padding: 18px 26px;
    }
  }
}
