.header{
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 5;
  // margin-bottom: -166px;
  // background-color: var(--charade-color);

  &__overlay {
    position: fixed;
    display: none;
    z-index: auto;
    top: 0;
    left: -1000%;
    right: 0;
    bottom: 0;
    // width: 100vw;
    // height: 100vh;
    background-color: rgba(0, 0, 0, 0.75);

  }

  &__content {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 3.291958985429034%;
    padding-top: 50px;
    // padding-top: clamp(26px, 50px);
    // padding-top: clamp(26px, ) 3.703703703703704%;

    // @media (max-width: 1400px) {
    //   // padding-left: 108px;
    //   // padding-right: 95px;
    //   padding-left: 7.883211678832117%;
    //   padding-right: 6.934306569343066%;
    // }

    @media (max-width: 1400px) {
      // padding-left: max(8%, 41px);
      // padding-right: max(7%, 34px);
      padding-left: 8%;
      padding-right: 7%;
      // padding-top: 3.703703703703704%;
      padding-top: max(3.703703703703704%, 35px);
    }
    @media (max-width: 768px) {
      padding-left: 5%;
      padding-right: 5%;
      padding-top: max(4.74%, 26px);
    }
  }

  &__user-menu {
    display: flex;
    gap: 11px;
    margin-top: -29px;
    margin-right: -5px;
    // margin-top: -1.67%;
    // margin-right: -0.3%;

    &-overlay {
      display: none;
    }

    @media (max-width: 1400px) {
      margin-right: 0;
    }
    @media (max-width: 760px) {
      position: fixed;

      top: 0;
      right: 0;
      flex-direction: column;
      align-items: center;
      // width: 100vw;
      width: min(100vw, 384px);
      height: 100vh;
      // height: auto;
      margin-top: 0;
      padding-top: 12.1875%;
      padding-bottom: 18%;
      background-color: var(--charade-color);
      transform: translateX(100%);
      transition: transform 0.3s ease-in-out;
    }

    &.menu--active {
      z-index: 1;
      transform: translateX(0);
      transition: transform 0.3s ease-in-out;

      // &::before {
      //   content: "";
      //   position: fixed;
      //   z-index: -1;
      //   top: 0;
      //   left: -1000%;
      //   right: 0;
      //   bottom: 0;
      //   // width: 100vw;
      //   // height: 100vh;
      //   background-color: rgba(0, 0, 0, 0.75);
      // }

      .header__links {
        height: auto;
      }

      // & .header__user-menu-overlay {
      //   position: fixed;
      //   display: block;
      //   z-index: -2;
      //   top: 0;
      //   left: -1000%;
      //   right: 0;
      //   bottom: 0;
      //   // width: 100vw;
      //   // height: 100vh;
      //   background-color: rgba(0, 0, 0, 0.75);
      // }
    }
  }

  &__logo {
    display: block;

    @media (max-width: 1400px) {
      // max-width: max(13.42857142857143%, 167px);
    }
    @media (max-width: 768px) {
      max-width: 167px;
    }
    @media (max-width: 576px) {
      max-width: 108px;
      margin-bottom: 2%;
    }

    &-user-menu {

      @media (min-width: 761px) {
        display: none;
      }

    }
  }
}

body .header {

  &__links {
    position: static;
    left: auto;
    bottom: auto;
    display: block;
    transform: translateX(0);

    @media (min-width: 761px) {
      display: none;
    }
    @media (max-width: 760px) {

      margin-bottom: 8%;

      .button-border {
        border: none;
        padding: 7.3% 60px;
        font-size: 16px;
        color: var(--light-color);
        background-color: transparent;
      }

    }


  }
}

.button-radius {

  @media (max-width: 760px) {
    width: 74%;
    font-size: 14px;

    @include mb (5.817%);
  }
}

.burger {
  align-self: flex-start;
  z-index: 5;
  // position: absolute !important;
  // top: 0;
  // right: 0;
  // align-self: self-end;
  // margin-top: -6%;
  // margin-right: 8%;
  // margin-bottom: -6%;

  @media (min-width: 761px) {
    display: none;
  }
  @include burger;

  @media (max-width:576px) {

  }
}


