.footer {
  @media (max-width: 320px) {
    margin-top: 99px;
  }

  &__container {
    padding-top: 111px;
    padding-bottom: max(27px, 3%);
    border-top-left-radius: 20px;
    border-top-right-radius: 20px;
    background: no-repeat url(../img/ellipse.png) 9.9% -16%, no-repeat url(../img/ellipse-bottom.png) right 7.8% bottom -44%;
    background-color: var(--charade-color);
    @media (max-width: 320px) {
      padding-top: 128px;
    }

    & .about__container {
      position: relative;
      &::before {
        position: absolute;
        top: -82px;
        right: -6px;
        width: 616px;
        height: 94px;
        overflow: hidden;
        content: "{ } { } { } { } { } { } { } { } { } { } { } { } { } { } { } { }";
        direction: rtl;
        font-size: 20px;
        letter-spacing: 0.67em;
        line-height: 2.35;
        color: var(--light-color);
        @media (max-width: 1280px) {
          right: 3px;
          width: 574px;
        }
        @media (max-width: 1100px) {
          width: 482px;
        }
        @media (max-width: 768px) {
          top: -95px;
          right: 3px;
          // width: 469px;
        }
        @media (max-width: 767px) {
          // width: 299px;
          // right: 3.4%;
        }
        @media (max-width: 592px) {
          // width: 299px;
          // height: 47px;
          // right: 0;
          // top: 59px;
        }
        @media (max-width: 592px) {


        }
        @media (max-width: 425px) {

        }
        @media (max-width: 398px) {
          left: 50%;
          transform: translateX(-50%);
        }
        @media (max-width: 320px) {
          top: -109px;
          right: 0;
          width: 469px;
        }
      }
    }
  }

  &__grid {
    display: grid;
    grid-template-columns: repeat(12, 8.3333333333%);
    @media (max-width: 768px) {
      grid-template-columns: repeat(8, 12.5%);
    }
    @media (max-width: 630px) {
      position: relative;
    }
  }

  &__title {
    grid-column: 1/-1;
    margin-bottom: 14px;
    color: var(--light-color);
    @media (max-width: 768px) {
      margin-bottom: 4.8vw;
    }
    @media (max-width: 320px) {
      margin-bottom: 13px;
    }
  }

  &__wrap {
    position: relative;
    grid-column: 4/-1;
    display: flex;
    align-items: center;
    @media (max-width: 768px) {
      grid-column: 3/-1;
      margin-bottom: 6px;
    }
    @media (max-width: 630px) {
      position: static;
      flex-direction: column;
    }
    @media (max-width: 390px) {
      grid-column: 1/-1;
      justify-content: center;
    }
    @media (max-width: 320px) {
      grid-column: 1/-1;
      justify-content: center;
    }
  }

  &__text {
    // position: relative;
    grid-column: 4/span 2;
    display: inline-block;
    color: var(--light-color);
    // @media (max-width: 980px) {
    //   grid-column: 3/span 2;
    //   margin-bottom: 6px;
    // }
    // @media (max-width: 768px) {
    //   grid-column: 3/span 2;
    //   margin-bottom: 6px;
    // }
    @media (max-width: 630px) {
      margin-left: -17%;
      margin-bottom: 6px;
    }
    // @media (max-width: 390px) {
    //   margin-left: 0;
    // }
    &::after {
      position: absolute;
      right: calc(100% + 40px);
      top: 0;
      width: 358px;
      height: 142px;
      overflow: hidden;
      content: "+ + + + + + + + + + + + + + + + + + + + + + + + + + + + + + + + + + + +";
      font-size: 20px;
      letter-spacing: 0.5em;
      line-height: clamp(20px, 200%, 38px);
      direction: rtl;

      @media (max-width: 1400px) {
        width: 332px;
      }
      @media (max-width: 1376px) {
        width: 332px;
      }
      @media (max-width: 1336px) {
        width: 293px;
      }
      @media (max-width: 1258px) {
        width: 256px;
      }
      @media (max-width: 1104px) {
        width: 216px;
      }
      @media (max-width: 954px) {
        width: 170px;
      }
      @media (max-width: 768px) {
        // top: -29px;
      }
      @media (max-width: 748px) {
        width: 130px;
      }
      @media (max-width: 630px) {
        left: 0;
        top: auto;
        right: auto;
        bottom: 66px;
        width: 219px;
      }
      @media (max-width: 320px) {
        left: 0;
        top: auto;
        right: auto;
        bottom: 66px;
      }
    }
  }

  &__link {
    display: block;
    text-align: center;
    grid-column: 6/-1;
    flex-grow: 1;
    max-width: 318px;
    min-width: 268px;
    align-self: center;
    margin: 0;
    margin-left: 43px;
    padding: 16px 20px;
    white-space: nowrap;
    @media (max-width: 768px) {
      grid-column: 5/-1;
      max-width: 302px;
      padding: 16px 20px;
      // margin-left: 10px;
    }
    @media (max-width: 630px) {
      margin-left: 0;
    }
    @media (max-width: 320px) {
      grid-column: 1/-1;
    }

    & span {
      position: relative;
      padding-left: 16px;
      & svg {
        position: absolute;
        content: "";
        width: 27px;
        height: 15px;
        left: -14px;
        top: 58%;
        transform: translateY(-50%);
        fill: var(--light-color);
      }
    }
    @include hover {
        & svg {
          fill: var(--monza-color);
        }
      }
  }

  &__copyright {
    grid-column: 1/-1;
    margin-top: max(98px, 11%);
    font-size: 12px;
    line-height: 15px;
    color: var(--light-color);
    @media (max-width: 630px) {
      margin-top: 200px;
    }
    @media (max-width: 320px) {
      margin-top: 200px;
    }
  }

}
