.benefits {

  &__container {
    max-width: 1200px;
    margin: 0 auto;
    padding: 0 20px;
    @media (max-width:768px) {
      margin-bottom: 71px;
      padding: 0 14px;
    }
    @media (max-width:320px) {
      margin-bottom: 74px;
      padding: 0 10px;
    }
  }

  &__title {
    margin-bottom: 39px;
    @media (max-width:768px) {
      margin-bottom: 28px;
    }
    @media (max-width:320px) {
      margin-bottom: 14px;
    }
  }

  &__grid {
    display: grid;
    grid-template-columns: repeat(2, 50%);
    gap: 1px;
    @media (max-width:576px) {
      column-gap: 0px;
    }
    @media (max-width:576px) {
      column-gap: 0px;
    }
  }

  &__item {
    grid-column: span 1;
    display: flex;
    // max-width: calc(100% - 1px);
    justify-content: center;
    align-items: center;
    flex-grow: 1;
    margin: 0;
    border-radius: 1.2em;
    padding: 28px 44px;
    background-color: var(--Indigo-color);
    text-align: center;
    font-weight: 500;
    font-size: 20px;
    color: var(--light-color);
    @media (max-width:768px) {
      padding: 26px 26px;
      font-size: 16px;
    }
    @media (max-width:576px) {
      grid-column: span 2;
      max-width: 100%;
    }
    @media (max-width:320px) {
      padding: 22px 23px;
      border-radius: 1.5em;
      font-size: 14px;
    }
  }
}
