.main {
  position: relative;
}

.margin-left-768 {

  @media (max-width:1300px) {
    margin-left: 25%;
  }

}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.centered {
  text-align: center;
}

.dis-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
  overscroll-behavior: none;
}

.page--ios .dis-scroll {
  position: relative;
}

.title {
  margin: 0;
  font-size: clamp(50px, 4.285714285714286vw, 60px);
  font-weight: 500;
  letter-spacing: -0.06em;
  color: var(--charade-color);

  @media (max-width:1400px) {
    // margin-left: clamp(-6px, calc(50px - 4vw), 14px);
  }
  @media (max-width:768px) {
    font-size: clamp(30px, 6.510416666666667vw, 50px);
  }
  @media (max-width:320px) {
    margin-left: 0;

  }
}

.text-base {
  margin: 0;
  font-size: 18px;
  line-height: 150%;
  color: var(--black-color);

  @media (max-width: 768px) {
    font-size: 16px;
  }
  @media (max-width: 500px) {
    font-size: 14px;
  }
}

.section-offset {
  margin-bottom: clamp(71px, 7.285714285714286vw, 102px);
  @media (max-width: 768px) {
    margin-bottom: clamp(54px, 9.244791666666667vw, 71px);
  }
  @media (max-width: 320px) {
    margin-bottom: 54px;
  }
}

.to-top {
  position: fixed;
  // right: calc((100% - 1260px) / 2);
  right: 5%;
  bottom: 80px;
  z-index: 1000;
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.3s ease-in-out, visibility 0.3s ease-in-out;
  @media (max-width: 1440px) {
    right: 20px;
    bottom: 20px;
  }
}

.to-top__icon {
  display: block;
  width: 40px;
  height: 40px;
  color: var(--jumbo-color);
  transition: color 0.3s ease-in-out;
  @include hover {
    color: var(--charade-color);
  }
  & svg {
    width: 100%;
    height: 100%;
    fill: currentColor;
  }
}

.to-top--active {
  opacity: 1;
  visibility: visible;
}
