.pricing {
  padding-top: 5px;
  background-color: var(--light-color);
  @media (max-width:768px) {
    padding-top: 0;
  }

  &__title {
    margin-bottom: 44px;
    @media (max-width:768px) {
      margin-bottom: 40px;
    }
    @media (max-width:320px) {
      margin-bottom: 63px;
    }
  }

  &__grid {
    @media (max-width:768px) {
      display: grid;
      grid-template-columns: repeat(8, 12.5%);
    }
  }

  &__wrap {
    grid-column: 3/-1;
    margin: 0 auto;
    max-width: 558px;
    @media (max-width:576px) {
      grid-column: 1/-1;
    }
  }

  &__item {
    margin: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-grow: 1;
    border-radius: 1.2em;
    padding: 28px 44px;
    font-size: 16px;
    line-height: 19px;
    text-align: center;
    color: var(--light-color);
    background-color: var(--charade-color);
    @include mb (10px);

    @media (max-width:320px) {
      padding: 20px 18px;
    }

    &.flex {
      display: flex;
      flex-direction: column;
      margin-bottom: 8%;
      @media (max-width:768px) {
        margin-bottom: 26px;
      }
      @media (max-width:320px) {
        margin-bottom: 30px;
      }

      & span {
        margin-top: 9px;
        margin-right: 10px;
      }
    }
  }

  &-price {
    &-old {
      text-decoration: line-through;
      color: #DA3739
    }
    &-new {
      font-weight: 600;
      font-size: 20px;
      line-height: 24px;
    }
  }

  &__text {
    margin: 0;
    margin-bottom: 10%;
    text-align: center;
    font-weight: 600;
    font-size: 18px;
    line-height: 22px;
    color: var(--black-color);
    @media (max-width:768px) {
      margin-bottom: 36px;
    }
    @media (max-width:320px) {
      margin-bottom: 43px;
    }
  }

  &__link {
    display: block;
    margin: 0 auto;
    max-width: 360px;
    text-align: center;
    & span {
      position: relative;
      padding-left: 30px;
      &::before {
        position: absolute;
        content: "";
        width: 25px;
        height: 18px;
        left: -10px;
        top: 50%;
        transform: translateY(-50%);
        // background-image: url(../img/logo-sm.svg);
        background-image: url("data:image/svg+xml,%3Csvg width='26' height='18' viewBox='0 0 26 18' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M5.04488 18C4.0268 17.9917 3.0681 17.7708 2.16877 17.3373C1.26952 16.8954 0.5466 16.2665 0 15.4506L1.13728 14.1758C1.64861 14.8897 2.23929 15.4336 2.90932 15.8076C3.57935 16.1815 4.3199 16.3685 5.13098 16.3685C5.38335 16.3685 5.62183 16.3526 5.84641 16.3209L5.04488 18ZM8.56927 10.6168V1.63175H1.79849V0H10.5V6.57219L8.56927 10.6168Z' fill='white'/%3E%3Cpath d='M6.75 18L15.1651 0H17.0849L25.5 18H23.4488L15.7174 1.10572H16.5063L8.77489 18H6.75ZM10.0635 13.1914L10.642 11.6486H21.345L21.9236 13.1914H10.0635Z' fill='white'/%3E%3C/svg%3E%0A");
      }
    }
  }
}
