// если вы хотите использовать sass-переменные - удалите root
// colors
:root {
  // base
  --font-family: "Inter", sans-serif;
  --content-width: 1870px;
  --container-offset: 15px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));

  // colors
  --light-color: #fff;
  --charade-color: #302d3d;
  --mischka-color: #dedce7; ;
  --monza-color: #D0082C;
  --Indigo-color: #4B6FCB;
  --black-color: #000;
  --jumbo-color: #7D7C85;
  --gallery-color: #efeeee;
}
